@import url(https://use.typekit.net/rut1tao.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
  padding: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.container {
  width: 80vw;
  max-width: 1350px;
  margin-left: auto;
  margin-right: auto;
  padding: 25px;
}

h2 {
  font-size: 40px;
  margin: 0;
  padding: 0;
  text-align: center;
}
b {
  font-family: realist, sans-serif;
  font-weight: 700;
  font-style: normal;
}
strong, h1, h2, h3, h4, h5, h6 {
  font-family: realist, sans-serif;
  font-weight: 800;
  font-style: normal;
}
span, p, textarea, input, li, a {
  font-family: realist, sans-serif;
  font-weight: 300;
  font-style: normal;
}

.headerMenu {
    position: absolute;
