}

.iconeMenu {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    height: 25px;
    width: 30px;
    background: transparent;
    border: none;
    cursor: pointer;
    padding: 0;
    margin-right: 10px;
}

.iconeMenu:focus {
    outline: none;
}

.linhaMenu {
    width: 25px;
    height: 3px;
    background: #fff;
    display: block;
    transition: all 0.4s ease-in-out;
    -moz-transition: all 0.4s ease-in-out;
    -webkit-transition: all 0.4s ease-in-out;
    -o-transition: all 0.4s ease-in-out;

}
.iconeMenu.menuAtivo .linhaMenu:nth-child(1) {
  -webkit-transform: translateY(8.3px) rotate(45deg);
          transform: translateY(8.3px) rotate(45deg);
}
.iconeMenu.menuAtivo .linhaMenu:nth-child(3) {
  -webkit-transform: translateY(-8.3px) rotate(-45deg);
          transform: translateY(-8.3px) rotate(-45deg);
} 
.iconeMenu.menuAtivo .linhaMenu:nth-child(2) {
  opacity: 0;
} 
